@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@500;700&display=swap');
.App {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  font-size: 18px;
  overflow-x: hidden;
}
button{
  cursor: pointer;
}
.error-message{
  width: 80%;
  background-color: red;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0px 10px;
  margin-bottom: 30px;
}
.landingPage {
  width: 100%;
}
.landingPage nav{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 70px 20px 50px;
}
.nav-links{
  display: flex;
}
.link-normal{
  text-decoration: none;
}
.nav-register{
  margin-right: 40px;
}
.nav-btn {
  color: gray;
  font-size: 18px;
}
.nav-btn:hover{
  color: rgb(63, 201, 235);
}
.first-text{
  margin: 70px 50px 40px 50px;
  width: 100%;
  display: flex;
  column-gap: 50px;
}
.first-text__left{
  line-height: 32px;
  width: 50%;
  margin-left: 50px;
}
.first-text__left h2{
  font-size: 52px;
  line-height: 60px;
  width: 10em;
  font-weight: 700;
}
.first-text__left p{
  font-size: 18px;
  color: gray;
  margin-bottom: 40px;
  width: 15em;
}
.btn-black{
  background-color: black;
  color: white;
  font-size: 14px;
  text-align: center;
  align-items: center;
  padding: 8px 3px;
  border-radius: 10px;
  width: 160px;
}
.first-text__right{
  width: 50%;
  display: flex;
  align-items: end;
}
.first-text__right .box{
  width: 400px;
  height: 270px;
  background: rgb(63, 201, 235);
  background: linear-gradient(90deg, rgb(63, 201, 235) 35%, rgb(231, 13, 177) 100%);
  border-radius: 70px;
}
.box img{
  width: 400px;
  height: 500px;
  position: relative;
  z-index: 999;
  margin-top: -230px;
}

.second-text{
  margin: 70px 50px;
}
.second-text__container{
  display: grid;
  column-gap: 30px;
  grid-template-columns: auto auto;
}
.about{
  display: grid;
  grid-template-columns: auto auto auto;
  column-gap: 30px;
}
.numbers{
  font-size: 32px;
  font-weight: 600;
  width: 12em;
}
.branches, .new-student, .comments{
  margin-top: 10px;
  width: 150px;
}
.text-blue{
  font-size: 28px;
  font-weight: 600;
  color: rgb(65, 181, 219);
  margin-bottom: 10px;
}
.number-text{
  font-size: 16px;
  color: gray;
}
.third-text{
  margin: 40px 50px;
}
.third-text__container{
  width: 90%;
  background: rgb(63, 201, 235);
  background: linear-gradient(90deg, rgb(63, 201, 235) 35%, rgb(231, 13, 177) 100%);
  border-radius: 70px;
  display: flex;
  padding: 20px 50px;
  column-gap: 30px;
}
.student-value{
  width: 50%;
}
.student-value p{
  font-size: 32px;
  font-weight: 700;
  margin-bottom: 70px;
  width: 10em;
}
.value-grid{
  width: 50%;
  display: flex;
  flex-wrap: wrap;

}
.grid-1{
  width: 200px;
  margin-bottom: 20px;
}
.grid-1__icon{
  width: 50px;
  height: 40px;
  background-color: white;
  border-radius: 15px;
  margin-bottom: 5px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.grid-icon {
  width: 30px;
  height: 30px;
  color: rgb(235, 63, 183);
  display: flex;
  justify-content: center;
  align-items: center;
}
.grid-icon-2{
  width: 30px;
  height: 30px;
  color: rgb(115, 63, 235);
  display: flex;
  justify-content: center;
  align-items: center;
}
.online-class{
  font-size: 18px;
  font-weight: 600;
  margin-bottom: 5px;
}
.grid-1__text{
  font-size: 16px;
}



/*Login page*/
.Login {
  width: 100%;
  min-height: 100vh;
  background: rgb(2, 2, 136, 0.9);
  margin: 0;
  padding: 80px 0;
  box-sizing: border-box;
}
.loginSection {
  width: 70%;
  min-height: 81vh;
  background: white;
  border-radius: 10px;
  box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.1);
  display: flex;
  flex-direction: column;
  margin: 0 auto;
  padding: 10px 10px;
  box-sizing: border-box;
}
.loginSection .loginCancel {
  width: 100%;
  float: right;
  text-decoration: none;
  cursor: pointer;
}
.loginSection .loginCancel .cancel {
  color: gray;
  font-size: 32px;
  float: right;
}
.loginFormSection {
  display: flex;
  justify-content: space-between;
  margin: 20px 50px 20px 30px;
  width: 100%;
}
.lefttLogin {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 10px;
}
.lefttLogin h3 {
  text-align: center;
}
.lefttLogin img {
  width: 350px;
}
.rightLogin {
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.rightLogin .logo {
  width: 80px;
  display: flex;
  justify-content: center;
}
.rightLogin .logo img {
  width: 100%;
}
.rightLogin .studentLogin {
  width: 100%;
  margin-top: 20px;
}
.studentForm {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.studentForm .text, .studentForm .email, .studentForm .password {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  width: 80%;
  height: 40px;
  padding-bottom: 5px;
  border: solid 1px black;
  margin-bottom: 30px;
}
.studentForm .text input, .studentForm .email input, .studentForm .password input {
  flex: 1;
  border: none;
  outline: none;
  color: gray;
  font-size: 22px;
}
.studentForm .text input::placeholder, .studentForm .email input::placeholder, .studentForm .password input::placeholder {
  opacity: .5s;
  letter-spacing: 3px;
}
.studentForm .text i, .studentForm .email i .formIcon, .studentForm .password i .formIcon {
  font-size: 24px;
  color: gray;
  width: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.studentForm .formButton {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 0 auto 50px auto;
  align-items: center;
}
.btnState{
  width: 70%;
}
.loginButton {
  width: 100%;
  background: navy;
  color: white;
  font-size: 22px;
  padding: 15px;
  border-radius: 20px;
  text-align: center;
  cursor: pointer;
}
.loginGoogle {
  display: flex;
  margin-bottom: 20px;
  width: 75%;
  justify-content: center;
  border: solid 1px rgb(2,0,36);
  padding: 15px;
  border-radius: 20px;
  cursor: pointer;
  column-gap: 15px;
}
.loginGoogle:hover {
  background: rgb(63,235,123);
  background: linear-gradient(90deg, rgba(63,235,123,1) 35%, rgba(63,170,235,1) 100%);
  transition: .5s;
  border: none;
}
.loginGoogleButton {
  font-size: 18px;
}
.google {
  font-size: 24px;
} 

/*Login page ends here*/
/*sidebar*/
.navBar .smallNav, .navMobile {
  display: none;
  visibility: hidden;
}
.sidebar {
  width: 200px;
  min-height: 100vh;
  border-top-right-radius: 100px;
  background: rgba(39, 39, 151, 1);
  height: 100vh;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  padding-left: 10px;
  transform: translate(0%);
}
.logoSidebar {
  display: flex;
}
.logoSidebar h4 {
  color: white;
}
.sidebarNav {
  margin-left: 8px;
  margin-top: 30px;
}
.navLinks {
  text-decoration: none;
  color: white;
}
.active .navLinkActive {
  width: 100%;
  background-color: rgba(23, 30, 70, 0.7);
  padding: 0 0 0 5px;
  border-top-left-radius: 35px;
  border-bottom-left-radius: 35px;
  right: 0;
}
.navLinks .navLinkActive {
  display: flex;
  cursor: pointer;
}
.navIcon {
  color: white;
  font-size: 22px;
  margin-top: 20px;
  margin-right: 18px;
}
.logOut {
  color: white !important;
  display: flex;
  margin-left: 10px;
  cursor: pointer;
}
.logOut .logOutIcon {
  font-size: 22px;
  margin-top: 20px;
  margin-right: 18px;
  color: white;
}
/*sidebar ends here */
.dashboard {  
  width: 100vww;
  min-height: 100vh;
  display: flex;
  background-color: rgb(23, 30, 70);
}
.dashboardContainer {
  width: calc(100% - 200px);
  min-height: 100vh;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}
.dashboardContainer .dashboardContent {
  width: 80%;
  height: 20em;
  background: rgb(63, 201, 235);
  background: linear-gradient(90deg, rgb(63, 201, 235) 35%, rgb(231, 13, 177) 100%);
  margin: 0 auto;
  display: flex;
  justify-content: center;
  border-radius: 15px;
}
.leftDashboard {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.leftDashboard h2 {
  color: white;
  font-size: 42px;
}
.leftDashboard span {
  color: blue;
  font-size: 32px;
  font-weight: 700;
}
.welcomeUser {
  line-height: 15px;
}
.rightDashboard {
  margin-left: 40px;
}
.rightDashboard .imgBox {
  width: 400px;
  position: relative;
}
.rightDashboard .imgBox img {
  width: 100%;
}
/*Courses*/
.courses {
  width: 100%;
  min-height: 100vh;
  display: flex;
  background-color: rgb(23, 30, 70);
}
.coursesContainer {
  width: calc(100% - 200px);
  display: flex;
  flex-direction: column;
}
.coursesContainer h2 {
  margin: 30px auto;
  text-align: center;
  color: white;
  font-size: 42px;
  font-weight: 700;
}
.coursesContainer .courseRender {
  width: 70%;
  margin: 0 auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  padding-bottom: 40px;
}
.courseLink {
  text-decoration: none;
  width: 150px;
  height: 150px;
  margin-right: 20px;
  margin-top: 15px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
 text-align: center;
}
.eachCourse {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
}
.courseLink:nth-child(1), .courseLink:nth-child(3), .courseLink:nth-child(5)  {
  background-color: rgb(27, 204, 86);
  color: navy;
}
.courseLink:nth-child(2), .courseLink:nth-child(4) {
  background-color: navy;
  color: rgb(15, 226, 86);
}

.eachCourse h5 {
  text-align: center;
}
 /*Single Course*/
 .singleCourses {
  width: 100%;
  min-height: 100vh;
  display: flex;
  background-color: rgb(23, 30, 70);
 }
 .singleCourseContainer {
  width: calc(100% - 200px);
  display: flex;
  flex-direction: column;
  padding-left: 20px;
  margin-left: 20px;
 }
 .singleLink {
   text-decoration: none;
 }
 .singleCourseBack {
   width: 100px;
   background-color: rgb(27, 89, 204);
   color: white;
   border-radius: 8px;
   padding: 5px 7px;
   font-size: 18px;
   margin-top: 30px;
   box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
   text-align: center;
 }
 .eachCourseContent {
   padding-bottom: 40px;
 } 
 .contentContainer {
   color: white;
   padding-bottom: 30px;
 }
 .contentContainer .content {
   width: 80%;
   max-width: 90%;
 }
 /*Single Course Ends*/
 /*Profile Page*/
 .profile {
   display: flex;
   background-color: rgb(23, 30, 70);
   width: 100%;
   min-height: 100vh;
 }
 .profileContainer {
   width: calc(100% - 220px);
   padding-left: 40px;
   margin: 10px 40px;
   color: white;
   line-height: 16px;
 }
 .profileContent {
   margin: 50px auto 20px auto;
   display: flex;
   flex-direction: column;
   align-items: center;
   justify-content: center;
 } 
 .profileContainer .avatar {
   width: 200px;
   height: 200px;
   border-radius: 100vw;
   object-fit: contain;
   border: solid 1px rgb(23, 30, 70);
   box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
 }
 .profileContainer .avatar img {
   width: 100%;
   border-radius: 100vw;
   object-fit: contain;
 }
 .profileDetails {
   width: 100%;
   display: flex;
   flex-wrap: wrap;
   justify-content: center;
   align-items: center;
   column-gap: 50px;
   margin-top: 50px;
 }
 .profileDetails .profileNameEmail {
   width: 40%;
   height: 70px;
   border: solid 1px white;
   border-radius: 10px;
   padding-left: 5px;
   padding-top: 5px;
   line-height: 5px;
 }
 .profileDetails .profileNameEmail span {
   color: greenyellow;
   font-size: 15px;
   padding: 7px 5px 1px 5px;
 }
 .profileDetails .profileNameEmail h5 {
   font-size: 18px;
 }
 .toggleLogin {
   color: navy;
   cursor: pointer;
   text-align: center;
   display: flex;
   justify-content: center;
   align-items: center;
 }
 /*Profile page ends*/
@media (max-width: 1024px) {
  .loginSection{
    width: 90%;
  }
  .rightDashboard .imgBox {
    width: 300px;
    top: -20px;
  }
}
@media (max-width: 960px) {
  .first-text__left{
    margin-left: 10px;
  }
  .second-text__container{
    grid-template-columns: auto;
  }
  .numbers{
    text-align: center;
    width: 100%;
  }
  .loginSection {
    padding-bottom: 10px;
  }
  .loginFormSection {
    margin-top: 5px;
    padding-bottom: 10px;
  }
  .lefttLogin img {
    width: 250px;
  }
  .rightLogin .studentLogin {
    margin-top: 5px;
    padding-bottom: 10px;
  }
  .studentForm .text input, .studentForm .email input, .studentForm .password input{
    font-size: 18px;
  }
  .loginGoogle, .loginButton {
    padding: 10px;
  }
  .studentForm .email input::placeholder, .studentForm .password input::placeholder {
    letter-spacing: 1px;
  }
  /*Mobile Sidebar*/
  .navBar .smallNav, .navMobile {
    width: 100%;
    display: flex;
    visibility: visible;
  }
  .navBar .desktopNav, .sidebar {
    display: none;
    visibility: hidden;
  }
  .navMobile {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px 20px;
    box-sizing: border-box;
  }
  .openNav {
    color: white;
    font-size: 28px;
    cursor: pointer;
    float: right;
  }
  .sideMenuBg {
    width: 50vw;
    height: 100vh;
    position: absolute;
    top: 0;
    right: 0;
    transform: translateX(0%);
    z-index: 105;
    background: rgba(39, 39, 151, 1);
    transition: .5s;
    visibility: visible;
  }
  .closeSidebar {
    transform: translateX(200%);
    transition: .5s;
  }
  .mobileNav {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    padding: 30px 20px;
  }
  .active {
    width: 100%;
    background-color: rgba(23, 30, 70, 0.7);
  }
  .navLinkActive {
    margin-bottom: 30px;
  }
  .mobileLinks {
    margin-top: 75px;
  }
  /*Mobile Sidebar ends*/
  .dashboard, .courses, .profile, .singleCourses {
    flex-direction: column;
    box-sizing: border-box;
  }
  .dashboardContainer, .singleCourseContainer, .profileContainer, .coursesContainer {
    width: 100%;
    box-sizing: border-box;
  }
  .coursesContainer, .coursesContainer .courseRender {
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .profileContainer {
    margin: 10px auto;
  }
  .singleCourseContainer {
    padding-left: 10px;
    margin: 0 auto;
    box-sizing: border-box;
    width: 100%;
    justify-content: center;
  }
  .contentContainer h2 {
    font-size: 22px;
  }
  .dashboardContainer .dashboardContent {
    width: 90%;
  }
  .landingMain .landingMainContent h1 {
    text-align: center;
  }
  
}
@media(max-width: 720px){

}
@media (max-width: 670px) {
  .loginFormSection {
    flex-direction: column;
    margin: 0;
  }
  .loginSection {
    height: 90%;
  }
  .lefttLogin { 
    text-align: center;
    margin-left: 0;
    align-items: center;
  }
  .lefttLogin img {
    width: 90%;
    margin: 0 auto;
  }
  .rightLogin {
    width: 100%;
    margin-top: 40px;
  }
  .rightLogin .logo {
    position: absolute;
    top: 70px;
    left: 0;
    margin-left: 10px;
  }
  .studentForm .text, .studentForm .email, .studentForm .password{
    width: 100%;
  }
  .loginGoogle {
    width: 90%;
  } 
  .btnState {
    width: 90%;
  }
  .loginGoogle, .loginButton {
    font-size: 18px;
  }
  .dashboardContainer {
    margin: 50px auto;
  }
  .dashboardContent {
    flex-direction: column;
    min-height: 100vh;
  }
  .welcomeUser {
    align-items: center;
    text-align: center;
    margin: auto;
    line-height: 10px;
    margin-bottom: 90px;
  }
  .leftDashboard h2 {
    text-align: center;
    margin-left: auto;
    margin-right: auto;
  }
  .rightDashboard .imgBox {
    width: 100%;
    left: 0;
    right: 0;
  }
}
@media (max-width:500px) {
  .landingPage nav{
    margin: 20px 10px 20px 10px;
  }
  .nav-register{
    margin-right: 20px;
  }
  .first-text{
    flex-direction: column;
    margin: 40px auto 40px auto;
    row-gap: 250px;
  }
  .first-text__left{
    margin: 0 20px;
    text-align: center;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .first-text__left h2{
    width: 100%;
    font-size: 42px;
    margin: 0;
  }
  .first-text__right{
    justify-content: center;
    align-items: center;
    width: 100%;
  }
  .second-text{
    width: 90%;
    margin: 30px 10px;
  }
  .second-text__container{
    grid-template-columns: auto;
    row-gap: 5px;

  }
  .about{
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    justify-content: center;
  }
  .numbers{
    font-size: 28px;
  }
  .third-text{ 
    margin: 20px auto;
    display: flex;
    justify-content: center;
  }
  .third-text__container{
    padding: 20px 10px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .student-value{
    width: 100%;
    display: flex;
    flex-direction: column;
    margin: 20px auto 50px auto;
    justify-content: center;
    align-items: center;
  }
  .student-value p{
    width: 100%;
    text-align: center;
    margin-bottom: 30px;
  }
  .value-grid{
    width: 100%;
  }
  .grid-1{
    width: 50%;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .error-message{
    width: 93%;
  }
  .leftDashboard span {
    font-size: 22px;
  }

  .sideMenuBg {
    width: 70vw;
  }
  .profileDetails {
    flex-direction: column;
    align-items: flex-start;
  }
  .profileDetails .profileNameEmail {
    width: 90%;
    margin-bottom: 20px;
  }
  .profileDetails .profileNameEmail h5 {
    font-size: 14px;
  }
}
